// Modern Story Timeline Component with Scroll Animations
interface TimelineItem {
  year: string;
  title: string;
  description: string;
}

interface StoryTimelineProps {
  timelineItems: TimelineItem[];
}

const StoryTimeline: React.FC<StoryTimelineProps> = ({ timelineItems }) => {
  const [visibleItems, setVisibleItems] = useState<Set<number>>(new Set());
  const itemRefs = useRef<Array<HTMLDivElement | null>>([]);

  useEffect(() => {
    const observers: IntersectionObserver[] = [];

    itemRefs.current.forEach((ref, index) => {
      if (!ref) return;

      const observer = new IntersectionObserver(
        ([entry]) => {
          if (entry.isIntersecting) {
            setVisibleItems((prev) => new Set(prev).add(index));
            observer.unobserve(ref);
          }
        },
        {
          threshold: 0.3,
          rootMargin: '0px 0px -100px 0px',
        }
      );

      observer.observe(ref);
      observers.push(observer);
    });

    return () => {
      observers.forEach((observer) => observer.disconnect());
    };
  }, []);

  return (
    <Box className="timeline-container">
      <Box className="timeline">
        {timelineItems.map((item, index) => {
          const isVisible = visibleItems.has(index);
          const isLeft = index % 2 === 0;

          return (
            <Box
              key={index}
              ref={(el: HTMLDivElement | null) => {
                itemRefs.current[index] = el;
              }}
              className={`timeline-item ${isLeft ? 'timeline-left' : 'timeline-right'} ${isVisible ? 'timeline-visible' : ''}`}
            >
              <Box className="timeline-marker-wrapper">
                <Box className="timeline-marker">
                  <Box className="timeline-marker-inner"></Box>
                </Box>
              </Box>
              <Box className="timeline-content">
                <Box className="timeline-content-inner">
                  <Typography variant="h6" className="timeline-year">
                    {item.year}
                  </Typography>
                  <Typography variant="h6" className="timeline-title">
                    {item.title}
                  </Typography>
                  <Typography variant="body2" className="timeline-description">
                    {item.description}
                  </Typography>
                  <Box className="timeline-content-glow"></Box>
                </Box>
              </Box>
            </Box>
          );
        })}
      </Box>
    </Box>
  );
};

